import request from '@/request/api'
/**===========基本信息接口 */

// 获取列表
export function getListApi(data = {}) {
    return request({
        url: '/workorder/RepairappAction/queryList.htm',
        method: 'post',
        data: data
    })
}

//详情
export function getDetailsApi(id = "") {
    return request({
        url: '/workorder/RepairappAction/detail.htm',
        method: 'post',
        params:{ id: id }
    })
}

// 删除
export function deleteRepairapp(id = "") {
    return request({
        url: '/workorder/RepairappAction/delete.htm',
        method: 'post',
        params: { id: id }
    })
}

//新建页面
export function addRepairapp(data = {}) {
    return request({
        url: '/workorder/RepairappAction/addRepairapp.htm',
        method: 'post',
        data: data
    })
}

//保障报修申请-审核前保存表单（保存草稿）
export function updateRepairapp(data = {}) {
    return request({
        url: '/workorder/RepairappAction/updateRepairapp.htm',
        method: 'post',
        data: data
    })
}

