<template>
  <div class="record__content">
    <div class="record__baseinfo">
      <dividerTitle :title="'基本信息'"></dividerTitle>
      <editForm
        :formConfig="formConfig"
        ref="editForm"
        :formData="editFormData"
      >
      </editForm>
    </div>
    <div
      class="process_score"
      v-if="editFormData.status && editFormData.status == 1"
    >
      <dividerTitle :title="'评分'"></dividerTitle>
      <processScore
        v-if="showScore"
        :scoreSetting="scoreSetting"
        :hideBtn="scoreHideBtn"
        :showScore.sync="showScore"
      ></processScore>
    </div>
    <div class="record__attach">
      <dividerTitle :title="'附件'"></dividerTitle>
      <attachTable
        :attachSetting="attachSetting"
        :hideBtn="hideBtn"
        ref="attachFile"
      ></attachTable>
    </div>

    <div
      class="record__workflow"
      v-if="
        pageType == 'details' &&
        editFormData.status &&
        editFormData.status !== 2
      "
    >
      <dividerTitle :title="'工作流'"></dividerTitle>
      <workFlow
        :currentFlowData="editFormData"
        :beforeSubmit="beforeSubmit"
        :otherParams="workFlowParams"
        @submitSuccess="workFlowSuccess"
      ></workFlow>
    </div>
  </div>
</template>

<script>
import {
  getDetailsApi, //获取详情
  updateRepairapp
} from "@/api/itService/repairApply";
export default {
  props: {
    formData: {
      type: Object,
      default: new Object(),
    },
    pageType: {
      type: String,
      default: "",
    },
    defaultSet: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      hadSeleteData: [],
      editFormData: { suitableWorkArr: [] },
      trainingType: {},
      formConfig: {
        itemArr: [
          {
            label: "申请日期",
            prop: "billDate",
            type: "date",
            // required: true,
            span: 8,
          },
          {
            label: "申请人",
            prop: "staffunName",
            idsStr: "employeeId",
            type: "user",
            trigger: "change",
            // required: true,
            methods: (obj) => {
              let { orgNodeName, orgNodeCode, mobile } = obj[0];
              this.$refs.editForm.setFormItem("depatName", orgNodeName);
              this.$refs.editForm.setFormItem("depatCode", orgNodeCode);
              this.$refs.editForm.setFormItem("phone", mobile);
            },
          },
          {
            label: "申请部门",
            prop: "depatName",
            id: "depatCode",
            placeholder: "选择申请人后自动带出",
            // required: true,
            type: "text",
          },
          {
            label: "联系号码",
            prop: "phone",
            // required: true,
            type: "text",
            placeholder: "选择申请人后自动带出",
            rules: {
              validator: function(rule, value, callback) {
                const reg = /^[0-9]*$/;
                if (!value) {
                  // return callback(new Error("请填写手机号码！"));
                } else if (!reg.test(value)) {
                  return callback(new Error("请填写正确的手机号码！"));
                } else {
                  callback();
                }
              },
              trigger: "change"
            }
          },

          {
            type: "select",
            label: "硬件类",
            prop: "fixedtype",
            seleteOption: this.$store.getters["dirtData"]("fixed_type"),
            formatter: (val) => {
              return this.$store.getters["fineDirtDataName"]("fixed_type", val);
            },
          },

          {
            type: "select",
            label: "办公软件类",
            prop: "softtype",
            // required: true,
            seleteOption: this.$store.getters["dirtData"]("soft_type"),
            formatter: (val) => {
              return this.$store.getters["fineDirtDataName"]("soft_type", val);
            },
          },
          {
            type: "select",
            label: "业务系统类",
            prop: "apptype",
            placeholder: "请选择业务系统类",
            // required: true,
            seleteOption: this.$store.getters["dirtData"]("app_type"),
            formatter: (val) => {
              return this.$store.getters["fineDirtDataName"]("app_type", val);
            },
          },
          {
            type: "select",
            label: "应用系统名称",
            prop: "appsysName",
            id: "appsysId",
            placeholder: "请选择应用系统名称",
            // required: true,
            seleteOption: this.$store.getters.systemType,
            methods: obj => {
              // console.log(obj);
              this.$refs.editForm.setFormItem("appsysId", obj.value);
              this.$refs.editForm.setFormItem("appsysName", obj.name);
            }
          }, 

          // {
          //   label: "审批人",
          //   prop: "confirmManName",
          //   idsStr: "confirmMan",
          //   type: "user",
          //   isHide: this.pageType == "details" && !this.isWorkFlowEdit,
          //   // required: true,
          //   trigger: "change",
          // },
          {
            label: "申请描述",
            prop: "appContent",
            type: "textarea",
            span: 24,
            rows: 4,
          },
          {
            label: "备注",
            prop: "remark",
            type: "textarea",
            span: 24,
            rows: 4,
          },
        ],
      },
      procInstType:null,
      //附件设置
      attachSetting: {
        title: "",
        select: false,
        billType: "asset_regist", //页面标识
        sourceId: "", //单据的ID
        btnArr: ["add", "delete", "save"],
      },
      //评分设置
      scoreSetting: {
        title: "",
        billType: this.procInstType, //页面标识
        sourceId: "", //单据的ID
        data: [],
        btnArr: ["save"],
      },
      scoreHideBtn: false,
      coverList: [],
      dialogVisible: false,
      activeName: "0",
      hideBtn: false,
      showScore: false,
      beforeSubmit: {
        //工作流提交前检测
        methods: (data) => {
          return this.submitBefore(data);
        },
      },
    };
  },
  computed: {
    //工作流额外参数
    workFlowParams() {
      let { procInstType } = this.editFormData;
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return {
          procInstType: procInstType || "",
          assignercodes: [this.editFormData.confirmMan],
        };
      }
      return { procInstType: procInstType || "" };
    },
    //是否是流程中可编辑
    isWorkFlowEdit() {
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      //row.status
      if (this.pageType == "add") {
        let defaultForm = {};
        if (this.defaultSet) {
          defaultForm = { ...this.formData };
        }
        this.editFormData = Object.assign({}, defaultForm, {
          billDate: this.$utils.getNowDay(),
        });
      } else {
        this.getDetails(this.formData.id);
      }
      if (this.pageType == "details") {
        if (!this.isWorkFlowEdit) {
          this.formConfig.itemArr.map((i) => (i.disabled = true));
        }
        this.attachSetting.sourceId = this.formData.id; //附件id
      }
      if (this.pageType == "score") {
        //评分
        this.scoreSetting.sourceId = this.formData.id;
        this.formConfig.itemArr.map((i) => (i.disabled = true));
        this.showScore = true; //显示评分
      }
    },

    //获取详情
    async getDetails(id) {
      let res = await getDetailsApi(id);
      let obj = res.data || {};
      this.procInstType = obj.procInstType;//附件页面标识
      this.attachSetting.sourceId = obj.id;//附件对应的流程ID，新建流程保存后即可添加附件
      this.editFormData = Object.assign({}, obj, {
        confirmMan: obj.empId,
        confirmManName: obj.zystaffunName,
      });
    },
    //提交前检测
    submitBefore(data) {
      //如果是起草节点
      if (this.isWorkFlowEdit) {
        return new Promise((resolve, reject) => {
          this.$refs.editForm
            .getFormData()
            .then((data) => {
              this.$set(data, "submitType", 1);
              // console.log(data);
              this.editFormData = Object.assign({}, data);
              
              updateRepairapp(data)
                .then((res) => {
                  resolve();
                })
                .catch((e) => {
                  reject(e);
                });
            })
            .catch((e) => {
              console.log(e)
              reject("表单信息未填写完整");
            });
        });
      } else {
        return true;
      }
    },
    //工作流提交成功之后
    workFlowSuccess() {
      this.$emit("update:pageType", "list");
    },
    //获取表单数据
    getFormData() {
      return this.$refs.editForm.getFormData();
    },
  },
};
</script>

<style scoped lang="scss">
</style>